import React, { Component } from 'react'
import Aux from '../../hoc/Aux';
import data from '../Data/governing.json'

class GoverningCouncil extends Component {
    state = {
        data: data
    }
    render() {
        const { data } = this.state;
        return (
            <Aux>
                {/* Banner home */}
                <section className="background">
                    <img src="assets/images/background.jpg" alt="no img" />
                </section>

                <section className="inner py-4">
                    <div className="container">
                        <div className="row">
                            <div className="col-md-12">
                                <h2 className="text-center font-weight-bold mt-3 mb-3">
                                    Our Governing Council
                                </h2>
                            </div>
                        </div>
                        <div className="main">
                            <hr />
                            <div className="row">
                                {data?.map((res, index) =>
                                    <div className="col-lg-3 col-md-4 col-sm-6 p-2" key={index}>
                                        <div className="custom-card">
                                            <div className="card-img">
                                                <img src={res.image} alt="no image" />
                                            </div>
                                            <h5 className="card-title">{res.name}</h5>
                                            <p className="card-subtitle">{res.title}</p>
                                            {res.telephone.length > 0
                                                ? <div className="card-content">
                                                    <span className="card-icon"><i className="fa fa-phone-square" /></span>
                                                    <p className="card-adr">{res?.telephone.map(item => item)}</p>
                                                </div>
                                                : null}
                                            {res.address
                                                ? <div className="card-content">
                                                    <span className="card-icon"><i className="fa fa-address-book" /></span>
                                                    <p className="card-adr">{res.address}</p>
                                                </div>
                                                : null}
                                            {res.email
                                                ? <div className="card-content">
                                                    <span className="card-icon"><i className="fa fa-envelope" /></span>
                                                    <p className="card-adr">{res.email}</p>
                                                </div>
                                                : null}
                                        </div>
                                    </div>)}
                            </div>
                        </div>
                    </div>
                </section>


                {/* <div className="grid">
                    
                </div> */}

            </Aux>
        )
    }
}

export default GoverningCouncil;
