import React, { Component } from 'react';
//import logo from './logo.svg';
import { Route, Switch, Redirect } from 'react-router-dom';
import './App.css';
import ContactUs from './Views/ContactUs/ContactUs';
import Employee from './Views/Employee/Employee';
import Events from './Views/Events/Events';
import GoverningCouncil from './Views/GoverningCouncil/GoverningCouncil';
import Home from './Views/Home/Home';
import Image from './Views/Image/Image';
import Objective from './Views/Objective/Objective';
import Societies from './Views/Societies/Societies';
import Video from './Views/Video/Video';
import WorkInProgress from './Views/WorkInProgress/WorkInProgress';
import Header from './Component/Common/Header/Header';
import Footer from './Component/Common/Footer/Footer';
import Aux from './hoc/Aux';
import NewApplication from './Views/Application/NewApplication';
import AlreadyApplied from './Views/Application/AlreadyApplied';
import Newapp from './Views/Application/Newapp';


class App extends Component {

  componentDidUpdate() {
    window.scrollTo({ top: 0, behavior: 'smooth' })
  }

  render() {
    return (
      <Aux>
        <Header />
        <Switch>

          <Route path="/" exact component={Home} />
          <Route path="/contactus" exact component={ContactUs} />
          <Route path="/employee" exact component={Employee} />
          <Route path="/events" exact component={Events} />
          <Route path="/governingcouncil" exact component={GoverningCouncil} />
          <Route path="/image" exact component={Image} />
          <Route path="/objective" exact component={Objective} />
          <Route path="/societies" exact component={Societies} />
          <Route path="/video" exact component={Video} />
          <Route path="/workInProgress" exact component={WorkInProgress} />
          <Route path="/newapplication" exact component={NewApplication} />
          <Route path="/newapp" exact component={Newapp} />
          <Route path="/alreadyapplied" exact component={AlreadyApplied} />
          <Redirect to="/404" from="*" />

        </Switch>
        <Footer />
      </Aux>
    );
  }

}

export default App;
