import React, { Component } from 'react';
import Aux from '../../hoc/Aux';
import { educationAction } from '../../Store/Actions/education';
import { connect } from 'react-redux';

class Newapp extends Component {
    state = {
        array: [{
            qualification: '',
            subject: '',
            marks: '',
            certificate_path: ''
        }],
        data: []
    }

    handlesubmit = () => {

        this.props.educationAction()
    }

    componentDidMount() {
        let educationDetails = [];
        // example {id:1592304983049, title: 'Deadpool', year: 2015}
        const addeducation = (ev) => {
            ev.preventDefault();  //to stop the form submitting
            let edu = {
                id: Date.now(),
                qualification: document.getElementById('title').value,
                subject: document.getElementById('yr').value,
                marks: document.getElementById('per').value,
                certificate_path: document.getElementById('file').value
            }
            educationDetails.push(edu);
            document.forms[0].reset(); // to clear the form for the next entries
            //document.querySelector('form').reset();

            //for display purposes only
            console.log('added', { educationDetails });

        }
        document.addEventListener('DOMContentLoaded', () => {
            document.getElementById('btn').addEventListener('click', addeducation);
        });
    }

    componentDidMount() {
        let experienceDetails = [];
        const experience = (ex) => {
            ex.preventDefault();
            let exp = {
                institute: document.getElementById('inst').value,
                work_attended: document.getElementById('work').value,
                from_date: document.getElementById('fromdate').value,
                to_date: document.getElementById('todate').value,
                certificate_path: document.getElementById('certpath').value
            }
            experienceDetails.push(exp);
            document.forms[0].reset();
            console.log('added', { experienceDetails });
        }
        document.addEventListener('DOMContentLoaded', () => {
            document.getElementById('exp_btn').addEventListener('click', experience);
        })
    }


    changeHandle = (event) => {
        event.preventDefault();
        this.setState({
            array: [{
                [event.target.name]: event.target.value
            }]
        })
    }

    addEducation = (event) => {
        event.preventDefault();
        const model = {
            qualification: this.state.array.qualification,
            subject: this.state.array.subject,
            marks: this.state.array.marks,
            certificate_path: this.state.array.certificate_path
        }
        this.setState({
            array: this.state.array.concat(model)
            //data: this.state.data.concat(model)
        })
        console.log(this.state.array);

    }


    // changeHandler = e => {
    //     this.setState({
    //         [e.target.name]: e.target.value
    //     })
    // }

    // submithandler = e => {
    //     e.preventDefault()
    //     console.log(this.state)
    // }

    render() {
        //const { qualification, subject, marks, certificate_path } = this.state
        return (
            <Aux>
                <section className="background">
                    <img src="assets/images/background.jpg" alt="no img" />
                </section>


                <div className="container">


                    <form onSubmit={this.submithandler}>
                        {this.state.array.map((res, i) => {
                            return (
                                <div className="row mt-3" key={i}>
                                    <div className="col-md-3">
                                        <div className="form-group">
                                            <label htmlFor="">qualification</label>
                                            {/* <input type="text"
                                                name="qualification"
                                                id="title"
                                                className="form-control"
                                                placeholder="qualification"
                                                value={qualification}
                                                onChange={this.changeHandler} /> */}
                                            <input type="text" name="qualification"
                                                className="form-control"
                                                onChange={(e, index) => this.changeHandle(e, index)} placeholder="" />
                                        </div>
                                    </div>
                                    <div className="col-md-3">
                                        <div className="form-group">
                                            <label htmlFor="">subject</label>
                                            {/* <input type="text"
                                                name="subject"
                                                id="yr" className="form-control"
                                                placeholder="subject"
                                                value={subject}
                                                onChange={this.changeHandler} /> */}
                                            <input type="text" name="subject" className="form-control" onChange={(e, index) => this.changeHandle(e, index)} placeholder="" />
                                        </div>
                                    </div>
                                    <div className="col-md-3">
                                        <div className="form-group">
                                            <label htmlFor="">marks</label>
                                            {/* <input type="number"
                                                name="marks" id="per"
                                                className="form-control"
                                                placeholder="marks"
                                                value={marks}
                                                onChange={this.changeHandler} /> */}
                                            <input type="text" name="marks" className="form-control" onChange={(e, index) => this.changeHandle(e, index)} placeholder=" " />
                                        </div>
                                    </div>
                                    <div className="col-md-3">
                                        <div className="form-group">
                                            <label htmlFor="">certificate_path</label>
                                            {/* <input type="file"
                                                name="certificate_path"
                                                id="file" className="form-control"
                                                placeholder="certificate_path"
                                                value={certificate_path}
                                                onChange={this.changeHandler} /> */}
                                            <input type="text" name="certificate_path" className="form-control" onChange={(e, index) => this.changeHandle(e, index)} placeholder=" " />
                                        </div>
                                    </div>
                                </div>
                            )

                        })}
                        <button type="submit" id="btn" className="btn btn-primary" onClick={this.addEducation}>Add Education Details</button>
                    </form>







                    <form>
                        {this.state.array.map((res, i) => {
                            return (
                                <div className="row">
                                    <div className="col-md-3">
                                        <div className="form-group">
                                            <label htmlFor="">Institute</label>
                                            <input type="text" id="inst" className="form-control"
                                                placeholder="Enter your Institute name" />
                                        </div>
                                    </div>
                                    <div className="col-md-2">
                                        <div className="form-group">
                                            <label htmlFor="">WorkAttended</label>
                                            <input type="text" id="work" className="form-control"
                                                placeholder="Enter your WorkAttended" />
                                        </div>
                                    </div>
                                    <div className="col-md-2">
                                        <div className="form-group">
                                            <label htmlFor="">From</label>
                                            <input type="date" id="fromdate" className="form-control"
                                            />
                                        </div>
                                    </div>
                                    <div className="col-md-2">
                                        <div className="form-group">
                                            <label htmlFor="">To</label>
                                            <input type="date" id="todate" className="form-control"
                                            />
                                        </div>
                                    </div>
                                    <div className="col-md-3">
                                        <div className="form-group" style={{ marginTop: '28px' }}>
                                            <input type="file" id="certpath" className="form-control"
                                            />
                                        </div>
                                    </div>
                                </div>
                            )
                        })}
                        <button type="submit" id="exp_btn" className="btn btn-primary" onClick={this.experience}>Add Experience Details</button>
                    </form>




                    <button type="button" onClick={this.handlesubmit}>Submit</button>
                </div>


            </Aux>
        )
    }
}


