import React, { Component } from 'react'
import Aux from '../../hoc/Aux';
import emaployees from '../Data/employee.json';

class Employee extends Component {
    state = {
        emaployees: emaployees
    }

    render() {
        const { emaployees } = this.state;
        return (
            <Aux>
                {/* Banner home */}
                <section className="background">
                    <img src="assets/images/background.jpg" alt="no img" />
                </section>

                <section className="inner py-4">
                    <div className="container">
                        <div className="row">
                            <div className="col-md-12">
                                <h2 className="text-center font-weight-bold mt-3 mb-3">
                                    Employees
                                </h2>
                            </div>
                        </div>
                        <div className="main">
                            <hr />
                            <div className="row">
                                {emaployees.map((item, index) =>
                                    <div className="col-md-6" key={index}>
                                        <div className="new-card">
                                            <div className="card-left">
                                                <div className="card-img">
                                                    <img src={item.image} alt="no image" />
                                                </div>
                                            </div>
                                            <div className="card-body">
                                                <h3 className="card-title">{item.name}</h3>
                                                <p className="card-subtitle">{item.title}</p>
                                                {item.telephone.length > 0
                                                    ? <div className="card-content">
                                                        <span className="card-icon"><i className="fa fa-phone-square" /></span>
                                                        <p className="card-adr">{item?.telephone.map(tele => tele)}</p>
                                                    </div>
                                                    : null}
                                                {item.address
                                                    ? <div className="card-content">
                                                        <span className="card-icon"><i className="fa fa-address-book" /></span>
                                                        <p className="card-adr">{item.address}</p>
                                                    </div>
                                                    : null}
                                                {item.email
                                                    ? <div className="card-content">
                                                        <span className="card-icon"><i className="fa fa-envelope" /></span>
                                                        <p className="card-adr">{item.email}</p>
                                                    </div>
                                                    : null}
                                            </div>
                                        </div>
                                    </div>)}

                            </div>
                        </div>
                    </div>
                </section>
            </Aux>
        )
    }
}

export default Employee;
