    edu = () => {
        let educationDetails = [];
        const addeducation = (ev) => {
            ev.preventDefault();  //to stop the form submitting
            let edu = {
                id: Date.now(),
                qualification: document.getElementById('title').value,
                subject: document.getElementById('yr').value,
                marks: document.getElementById('per').value,
                certificate_path: document.getElementById('file').value
            }
            educationDetails.push(edu);
            const clear = document.getElementById("eduId")
            clear.reset();
            // document.forms[0].reset(); // to clear the form for the next entries
            this.setState({
                educationDetails: educationDetails
            })


        }
        document.addEventListener('DOMContentLoaded', () => {
            document.getElementById('btn').addEventListener('click', addeducation);
        });
    }

    exp = () => {
        let experienceDetails = [];
        const addexperience = (ex) => {
            ex.preventDefault();
            let exp = {
                institute: document.getElementById('inst').value,
                work_attended: document.getElementById('work').value,
                from_date: document.getElementById('fromdate').value,
                to_date: document.getElementById('todate').value,
                certificate_path: document.getElementById('certpath').value
            }
            experienceDetails.push(exp);
            // document.forms[0].reset();
            const clear = document.getElementById("expId")
            clear.reset();
            this.setState({
                experienceDetails: experienceDetails
            })
        }
        document.addEventListener('DOMContentLoaded', () => {
            document.getElementById('exp_btn').addEventListener('click', addexperience);
        })
    }





    onresumeChange = (event) => {
        const toBase64 = file => new Promise((resolve, reject) => {
            const reader = new FileReader();
            reader.readAsDataURL(file);
            reader.onload = () => resolve(reader.result);
            reader.onerror = error => reject(error);
        });

        if (event.target.files) {
            let filesAmount = event.target.files.length;
            this.setState({
                resumefile_type: event.target.files[0].type
            });
            // let i;
            console.log("on image change")
            for (let i = 0; i < filesAmount; i++) {
                let reader = new FileReader();
                reader.onload = function (ev) {

                }.bind(this);
                // reader.readAsDataURL(event.target.files[i])
                var promiseA = toBase64(event.target.files[i]);
                promiseA.then((result) => {
                    const img = result
                    this.setState({
                        resume_path: img
                    })
                });
            }
        }
    }


