import React, { Component } from 'react';
import Aux from '../../hoc/Aux';
import { connect } from 'react-redux';
import { GetApplicationDetails } from '../../Store/Actions/education';

class AlreadyApplied extends Component {
    state = {
        applicationId: '',
        dtauytayus: "asdj"
    }

    handleChange = (event) => {
        event.preventDefault();
        this.setState({
            [event.target.name]: event.target.value
        });
    }
    getData = (event) => {
        event.preventDefault();
        const url = "getapplicantdetails";
        const body = {
            applicationId: this.state.applicationId
        }
        this.props.GetApplicationDetails(url, body);
    }

    render() {
        return (
            <Aux>
                {/* Banner home */}
                <section className="background">
                    <img src="assets/images/background.jpg" alt="no img" />
                </section>
                <div className="container">
                    <div className="row justify-content-center">
                        <div className="col-md-5 card my-4">
                            <form onSubmit={this.getData}>
                                <div className="input-group mb-3 mt-3 ">
                                    <input type="text" className="form-control"
                                        name="applicationId"
                                        onChange={this.handleChange}
                                        placeholder="Enter Application Id"
                                        aria-label="Recipient's username"
                                        aria-describedby="basic-addon2" />
                                    <div className="input-group-append">
                                        <button className="btn btn-info" type="submit"><i className="fa fa-search" aria-hidden="true"></i>&nbsp;&nbsp;Search Application!</button>
                                    </div>
                                </div>
                            </form>
                        </div>
                    </div>

                    {this.props.GroupReducer.applicationData.status ? (
                        <div className="row">

                            <div className="col-md-12">
                                <div className="card mb-3">
                                    <div className="row no-gutters">
                                        <div className="col-md-4">
                                            <img src="assets/images/inner/Men_Placeholder.png" className="card-img" alt="no img" />
                                        </div>
                                        <div className="col-md-8">
                                            <div className="card-body">
                                                <h5 className="card-title mb-2"> ID :{this.props.GroupReducer.applicationData.applicationId}</h5>
                                                <h6> Name: {this.props.GroupReducer.applicationData.first_name} {this.props.GroupReducer.applicationData.last_name}</h6>
                                                <p className="card-text">
                                                    {this.props.GroupReducer.applicationData.job}
                                                    <br />
                                                    <strong>Address:</strong>
                                                    <br />
                                                    {this.props.GroupReducer.applicationData.address},
                                                    {this.props.GroupReducer.applicationData.street},
                                                    {this.props.GroupReducer.applicationData.city},
                                                    {this.props.GroupReducer.applicationData.state} - {this.props.GroupReducer.applicationData.pin}
                                                    <br />
                                                    {this.props.GroupReducer.applicationData.applicationId}
                                                    <br />
                                                    <strong>Dob:</strong> {this.props.GroupReducer.applicationData.applicationId}
                                                    <br />
                                                    <strong>Mobile:</strong> {this.props.GroupReducer.applicationData.mobile}
                                                </p>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>

                            <div className="col-md-12">
                                <h3 className="text-info my-4">Education Details</h3>
                                <table className="table">
                                    <thead>
                                        <tr>
                                            <th scope="col">ID</th>
                                            <th scope="col">Qualification</th>
                                            <th scope="col">Subject</th>
                                            <th scope="col">Marks</th>
                                            <th scope="col">Certificate</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {this.props.GroupReducer.applicationData.educationDetails ?.map((res, i) => (<tr key={i}>
                                            <th scope="row">{i + 1}</th>
                                            <td>{res.qualification}</td>
                                            <td>{res.subject}</td>
                                            <td>{res.marks}</td>
                                            <td>File</td>
                                        </tr>
                                        ))}
                                    </tbody>
                                </table>
                            </div>

                            <div className="col-md-12">
                                <h3 className="text-info my-4">Experience</h3>
                                <table className="table">
                                    <thead>
                                        <tr>
                                            <th scope="col">ID</th>
                                            <th scope="col">Institute</th>
                                            <th scope="col">Work Attended</th>
                                            <th scope="col">From Date</th>
                                            <th scope="col">To Date</th>
                                            <th scope="col">Certificate</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {this.props.GroupReducer.applicationData.experienceDetails ?.map((res, i) => (<tr key={i}>
                                            <th scope="row">{i + 1}</th>
                                            <td>{res.institute}</td>
                                            <td>{res.work_attended}</td>
                                            <td>{res.from_date}</td>
                                            <td>{res.to_date}</td>
                                            <td>File</td>
                                        </tr>
                                        ))}
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    ) : (null)}



                </div>
            </Aux>
        )
    }
}


const mapsStatToProps = (state) => {
    const { GroupReducer } = state
    return {
        GroupReducer
    }
}

const mapsDispatchToProps = (dispatch) => {
    return {
        GetApplicationDetails: (url, data) => dispatch(GetApplicationDetails(url, data))
    }
}

export default connect(mapsStatToProps, mapsDispatchToProps)(AlreadyApplied);
