import Axios from '../../hoc/axios';
import * as actionType from '../Actions/types'
import { toast } from 'react-toastify';

export const educationAction = (url, body) => {
    // debugger
    return (dispatch) => {
        // dispatch({
        //     type: "update",
        //     payload: data
        // })

        Axios.post(url, body)
            .then(
                res => {
                    if (res.data.status === "OK") {
                        toast.success("Success");
                        localStorage.setItem("id", res.data.data.userId);
                        localStorage.setItem("token", res.data.data.token);
                        document.getElementById("nacsaform").reset()
                        document.getElementById("eduId").reset()
                        document.getElementById("expId").reset()
                        //$this.push("/transactions");
                    } else {
                        // alert("error")
                        toast.error("Something went wrong");
                    }
                }
            ).catch(
                err => {
                    console.log(err);
                    toast.error("Something went wrong");
                }
            )
    }
}



export const GetApplicationDetails = (url, body) => {
    // debugger
    return (dispatch) => {
        Axios.post(url, body)
            .then(
                res => {
                    if (res.data.data) {
                        // toast.success("Success");
                        dispatch({
                            type: actionType.DATA,
                            payload: res.data.data
                        })
                    } else {
                        //alert("error")
                        // toast.error("Error");
                        toast.error("Something went wrong");
                    }
                }
            ).catch(
                err => {
                    console.log(err);
                    toast.error("Something went wrong");
                }
            )
    }
} 