import * as actionTypes from '../Actions/types';
const INITIAL = {
    applicationData: {}
}

const GroupReducer = (state = INITIAL, action) => {
    switch (action.type) {
        case actionTypes.DATA:
            return {
                ...state,
                applicationData: action.payload
            }

        default:
            return state;
    }
}

export default GroupReducer;